import { Schema, Transferable } from '@livestore/utils/effect'

export class ServiceWorkerWebmeshEdgePort extends Schema.TaggedStruct('ServiceWorker.WebmeshEdgePort', {
  port: Transferable.MessagePort,
  source: Schema.Union(
    Schema.TemplateLiteral('contentscript-iframe-', Schema.Number),
    Schema.TemplateLiteral('devtools-panel-', Schema.Number),
  ),
  tabId: Schema.Number,
}) {}

export const ContentscriptIframeSearchParamsSchema = Schema.Struct({
  secret: Schema.String,
  tabId: Schema.NumberFromString,
})
